header {
	width: 100%;
	position: fixed;
	z-index: 5;
	text-align: center;
	align-items: center;
	justify-content: space-between;
	padding-top: 20px;
	padding-bottom: 30px;
	background: rgba(68, 46, 24, 1);

	> .inner {
		width: 100%;
		margin: 5px auto;
		padding-bottom: 10px;

		> #gnb {
			display: inline-flex;
			color: rgba(241, 223, 199, 1);
			font: normal 20px 'Abhaya Libre ', cursive;

			> li {
				width: 120px;
				margin: 20px 0px 5px;

				> a {
					display: block;
					color: rgba(241, 223, 199, 1);
					text-align: center;
					font: normal 20px 'Libre Caslon Text', serif;
					font-family: 'Libre Caslon Text', serif;
					&:hover {
						color: rgba(192, 127, 87, 1);
					}
				}
			}

			> h1 {
				margin: 0px 45px;
				> a {
					letter-spacing: -5px;
					font: normal 50px 'Libre Caslon Text', serif;
					color: rgba(241, 223, 199, 1);
				}
			}
		}
		// #util {
		// 	position: absolute;
		// 	top: 20px;
		// 	right: 40px;
		// 	li {
		// 		float: left;
		// 		margin-right: 20px;
		// 		a {
		// 			font: 12px/1 'Lora';
		// 			color: #000;
		// 		}
		// 	}
		// }
		.fa-bars-staggered {
			font-size: 30px;
			position: absolute;
			bottom: 44px;
			right: 40px;
			//display: none;
			color: rgba(173, 170, 155, 1);
			cursor: pointer;
		}
	}
	&.main {
		background: rgba(68, 46, 24, 1);
		width: 100%;
		height: 80px;
		position: absolute;
		padding-bottom: 90px;
		top: 0;
		left: 0;
		z-index: 2;
		> .inner {
			width: 100%;
			margin: 5px auto;
			padding-bottom: 0px;

			> #gnb {
				right: 5vw;
				> li {
					> a {
						display: block;
						color: rgba(241, 223, 199, 1);
						text-align: center;
						font: normal 20px 'Libre Baskerville', serif;
						&:hover {
							color: rgba(192, 127, 87, 1);
						}
					}
				}

				> h1 {
					margin: 0px 45px;

					> a {
						letter-spacing: -5rem;
						font: normal 50px 'Libre Caslon Text', serif;
						color: rgba(241, 223, 199, 1);
						letter-spacing: -5px;
					}
				}
			}
			.fa-bars-staggered {
				color: rgba(241, 223, 199, 1);
				display: none;
			}
		}
	}
	&.sub {
		background: rgba(68, 46, 24, 1);
		width: 100%;
		height: 80px;
		position: absolute;
		padding-bottom: 90px;
		top: 0;
		left: 0;
		z-index: 2;
		> .inner {
			width: 100%;
			margin: 5px auto;
			padding-bottom: 0px;

			> #gnb {
				right: 5vw;
				> li {
					> a {
						display: block;
						color: rgba(241, 223, 199, 1);
						text-align: center;
						font: normal 20px 'Libre Baskerville', serif;
						&:hover {
							color: rgba(192, 127, 87, 1);
						}
					}
				}

				> h1 {
					margin: 0px 45px;

					> a {
						letter-spacing: -5rem;
						font: normal 50px 'Libre Caslon Text', serif;
						color: rgba(241, 223, 199, 1);
						letter-spacing: -5px;
					}
				}
			}
			.fa-bars-staggered {
				color: rgba(241, 223, 199, 1);
				display: none;
			}
		}
	}
}

